@import "../../../../common/style/sass/variables";
@import "../../../../common/style/sass/mixins";

.container {

  background-color: #e9fce5;
  padding: 30px 0 50px 0;

  .h2 {
    @include container();
    @include text(28px, 800);
    text-align: center;
    margin-bottom: 30px;
  }

  .dev {
    @include container();
    @include flex(row, flex-start, center, wrap);
    gap: 30px;
  }
}
